import { useState } from 'react';
import { Link, useNavigate, useLocation } from 'react-router-dom';
import { 
  Users, 
  Calendar, 
  ClipboardList, 
  CreditCard, 
  BarChart3, 
  Settings, 
  LogOut, 
  ChevronLeft, 
  ChevronRight,
  Bell,
  Package,
  Layers
} from 'lucide-react';
import { cn } from '@/lib/utils';
import { Button } from '@/components/ui/button';
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar';
import { Popover, PopoverContent, PopoverTrigger } from '@/components/ui/popover';
import { 
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
} from "@/components/ui/alert-dialog";
import { toast } from 'sonner';

type SidebarLink = {
  label: string;
  href: string;
  icon: React.ElementType;
  roles: string[];
};

export default function DashboardLayout({ children, userRole = 'admin' }: { 
  children: React.ReactNode;
  userRole?: 'admin' | 'doctor' | 'receptionist' | 'optometrist';
}) {
  const [collapsed, setCollapsed] = useState(false);
  const [logoutDialogOpen, setLogoutDialogOpen] = useState(false);
  const navigate = useNavigate();
  const location = useLocation();

  // Get username based on role
  const getUserInfo = () => {
    switch(userRole) {
      case 'admin':
        return { name: 'Raj', role: 'Admin' };
      case 'doctor':
        return { name: 'Dr. Mehta', role: 'Doctor' };
      case 'receptionist':
        return { name: 'Priya', role: 'Receptionist' };
      case 'optometrist':
        return { name: 'Vikram', role: 'Optometrist' };
      default:
        return { name: 'User', role: 'Staff' };
    }
  };

  const userInfo = getUserInfo();

  const sidebarLinks: SidebarLink[] = [
    { label: 'Queue', href: '/dashboard/queue', icon: ClipboardList, roles: ['admin', 'doctor', 'receptionist', 'optometrist'] },
    { label: 'Appointments', href: '/dashboard/appointments', icon: Calendar, roles: ['admin', 'receptionist'] },
    { label: 'Patients', href: '/dashboard/patients', icon: Users, roles: ['admin', 'receptionist', 'doctor'] },
    { label: 'Billing', href: '/dashboard/billing', icon: CreditCard, roles: ['admin', 'receptionist'] },
    { label: 'Reports', href: '/dashboard/reports', icon: BarChart3, roles: ['admin'] },
    { label: 'Services', href: '/dashboard/services', icon: Package, roles: ['admin'] },
    { label: 'Inventory', href: '/dashboard/inventory', icon: Layers, roles: ['admin'] },
    { label: 'Settings', href: '/dashboard/settings', icon: Settings, roles: ['admin'] },
  ];

  // Filter links based on user role
  const filteredLinks = sidebarLinks.filter(link => link.roles.includes(userRole));

  // Get current page title
  const getCurrentPageTitle = () => {
    const currentPath = location.pathname;
    const currentLink = sidebarLinks.find(link => currentPath.includes(link.href));
    
    if (currentPath.includes('/emr/')) {
      return 'Patient EMR';
    }
    
    return currentLink?.label || 'Dashboard';
  };

  const handleLogout = () => {
    toast.success('Logged out successfully');
    navigate('/');
  };

  return (
    <div className="min-h-screen flex bg-background">
      {/* Sidebar */}
      <div 
        className={cn(
          "bg-card border-r border-border flex flex-col transition-all duration-300",
          collapsed ? "w-16" : "w-64"
        )}
      >
        {/* Sidebar Header */}
        <div className="p-4 border-b border-border flex items-center justify-between">
          <Link to="/dashboard/queue" className="flex items-center gap-2 overflow-hidden">
            <img 
              src="https://storage.googleapis.com/fenado-ai-farm-public/generated/bae86827-3b88-4061-92bb-2de0b1eec00d.webp" 
              alt="MedMitra Logo" 
              className="h-8 w-8 flex-shrink-0" 
            />
            {!collapsed && (
              <div>
                <span className="text-foreground font-semibold">MedMitra</span>
                <p className="text-xs text-muted-foreground">Dholakia Clinic</p>
              </div>
            )}
          </Link>
          <Button 
            variant="ghost" 
            size="icon" 
            onClick={() => setCollapsed(!collapsed)}
            className="text-muted-foreground hover:text-foreground"
          >
            {collapsed ? <ChevronRight size={18} /> : <ChevronLeft size={18} />}
          </Button>
        </div>
        
        {/* Sidebar Links */}
        <div className="flex-1 py-4 overflow-y-auto">
          <nav className="space-y-1 px-2">
            {filteredLinks.map((link) => {
              const isActive = location.pathname.includes(link.href);
              const Icon = link.icon;
              
              return (
                <Link
                  key={link.href}
                  to={link.href}
                  className={cn(
                    "flex items-center gap-3 px-3 py-2 rounded-md transition-colors",
                    isActive 
                      ? "bg-primary/10 text-primary" 
                      : "text-muted-foreground hover:bg-muted hover:text-foreground"
                  )}
                >
                  <Icon size={20} />
                  {!collapsed && <span>{link.label}</span>}
                </Link>
              );
            })}
          </nav>
        </div>
        
        {/* Logout Button */}
        <div className="p-4 border-t border-border">
          <Button
            variant="ghost"
            className={cn(
              "w-full flex items-center gap-3 text-muted-foreground hover:text-destructive",
              collapsed ? "justify-center" : "justify-start"
            )}
            onClick={() => setLogoutDialogOpen(true)}
          >
            <LogOut size={20} />
            {!collapsed && <span>Logout</span>}
          </Button>
        </div>
      </div>
      
      {/* Main Content */}
      <div className="flex-1 flex flex-col overflow-hidden">
        {/* Top Header */}
        <header className="h-16 bg-card border-b border-border flex items-center justify-between px-4 md:px-6">
          <h1 className="text-xl font-semibold">{getCurrentPageTitle()}</h1>
          
          <div className="flex items-center gap-4">
            <Popover>
              <PopoverTrigger asChild>
                <Button variant="ghost" size="icon" className="relative">
                  <Bell size={20} />
                  <span className="absolute top-0 right-0 h-2 w-2 rounded-full bg-primary"></span>
                </Button>
              </PopoverTrigger>
              <PopoverContent className="w-80" align="end">
                <div className="space-y-4">
                  <h3 className="font-medium text-sm">Notifications</h3>
                  <div className="space-y-2">
                    <div className="bg-muted/50 p-3 rounded-md">
                      <p className="text-sm font-medium">Appointment reminder sent for Patient #123</p>
                      <p className="text-xs text-muted-foreground mt-1">5 minutes ago</p>
                    </div>
                    <div className="bg-muted/50 p-3 rounded-md">
                      <p className="text-sm font-medium">Daily report is ready</p>
                      <p className="text-xs text-muted-foreground mt-1">30 minutes ago</p>
                    </div>
                    <div className="bg-muted/50 p-3 rounded-md">
                      <p className="text-sm font-medium">Dr. Mehta completed 15 consultations today</p>
                      <p className="text-xs text-muted-foreground mt-1">2 hours ago</p>
                    </div>
                  </div>
                  <Button variant="outline" className="w-full">View All</Button>
                </div>
              </PopoverContent>
            </Popover>
            
            <div className="flex items-center gap-3">
              <div className="hidden md:block">
                <p className="text-sm font-medium">Welcome, {userInfo.name}</p>
                <p className="text-xs text-muted-foreground">{userInfo.role}</p>
              </div>
              <Avatar>
                <AvatarImage src={`https://api.dicebear.com/6.x/initials/svg?seed=${userInfo.name}`} />
                <AvatarFallback>{userInfo.name[0]}</AvatarFallback>
              </Avatar>
            </div>
          </div>
        </header>
        
        {/* Page Content */}
        <main className="flex-1 overflow-auto p-4 md:p-8">
          {children}
        </main>
      </div>
      
      {/* Logout Confirmation Dialog */}
      <AlertDialog open={logoutDialogOpen} onOpenChange={setLogoutDialogOpen}>
        <AlertDialogContent>
          <AlertDialogHeader>
            <AlertDialogTitle>Are you sure you want to logout?</AlertDialogTitle>
            <AlertDialogDescription>
              You will be redirected to the home page.
            </AlertDialogDescription>
          </AlertDialogHeader>
          <AlertDialogFooter>
            <AlertDialogCancel>Cancel</AlertDialogCancel>
            <AlertDialogAction onClick={handleLogout}>Yes, Logout</AlertDialogAction>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>
    </div>
  );
}