import { useState } from 'react';
import { motion } from 'framer-motion';
import { DashboardLayout } from '@/components/layout';
import { Button } from '@/components/ui/button';
import { Card, CardContent } from '@/components/ui/card';
import { Dialog, DialogContent, DialogDescription, DialogFooter, DialogHeader, DialogTitle } from '@/components/ui/dialog';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar';
import { CalendarIcon, ChevronLeft, ChevronRight, Clock, Plus, Search, UserPlus } from 'lucide-react';
import { toast } from 'sonner';

// Types
type Appointment = {
  id: string;
  patientName: string;
  patientId: string;
  time: string;
  duration: string;
  doctor: string;
  status: 'scheduled' | 'in-progress' | 'completed' | 'cancelled';
  type: 'consultation' | 'follow-up' | 'procedure';
};

// Days of the week
const daysOfWeek = ['Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday', 'Sunday'];

// Time slots
const timeSlots = [
  '09:00 AM', '09:30 AM', '10:00 AM', '10:30 AM', '11:00 AM', '11:30 AM',
  '12:00 PM', '12:30 PM', '01:00 PM', '01:30 PM', '02:00 PM', '02:30 PM',
  '03:00 PM', '03:30 PM', '04:00 PM', '04:30 PM', '05:00 PM', '05:30 PM'
];

// Mock appointments data
const mockAppointments: Record<string, Appointment[]> = {
  'Monday': [
    {
      id: 'a1',
      patientName: 'Rahul Singh',
      patientId: 'DCLNC-2024-101',
      time: '09:30 AM',
      duration: '30 min',
      doctor: 'Dr. Mehta',
      status: 'scheduled',
      type: 'consultation'
    },
    {
      id: 'a2',
      patientName: 'Priya Sharma',
      patientId: 'DCLNC-2024-102',
      time: '11:00 AM',
      duration: '30 min',
      doctor: 'Dr. Mehta',
      status: 'scheduled',
      type: 'follow-up'
    }
  ],
  'Tuesday': [
    {
      id: 'a3',
      patientName: 'Vikram Desai',
      patientId: 'DCLNC-2024-105',
      time: '10:00 AM',
      duration: '1 hour',
      doctor: 'Dr. Mehta',
      status: 'scheduled',
      type: 'procedure'
    }
  ],
  'Wednesday': [
    {
      id: 'a4',
      patientName: 'Meera Joshi',
      patientId: 'DCLNC-2024-104',
      time: '02:30 PM',
      duration: '30 min',
      doctor: 'Dr. Mehta',
      status: 'scheduled',
      type: 'follow-up'
    }
  ],
  'Thursday': [],
  'Friday': [
    {
      id: 'a5',
      patientName: 'Amit Patel',
      patientId: 'DCLNC-2024-103',
      time: '04:00 PM',
      duration: '30 min',
      doctor: 'Dr. Mehta',
      status: 'scheduled',
      type: 'consultation'
    }
  ],
  'Saturday': [],
  'Sunday': []
};

export default function AppointmentsPage() {
  const [currentDay, setCurrentDay] = useState(0); // Monday by default
  const [showAppointmentDialog, setShowAppointmentDialog] = useState(false);
  const [showAppointmentDetails, setShowAppointmentDetails] = useState(false);
  const [selectedAppointment, setSelectedAppointment] = useState<Appointment | null>(null);
  const [selectedTimeSlot, setSelectedTimeSlot] = useState<string | null>(null);
  
  // Get appointments for the current day
  const currentDayAppointments = mockAppointments[daysOfWeek[currentDay]] || [];
  
  // Handle navigation between days
  const goToPreviousDay = () => {
    setCurrentDay((prev) => (prev === 0 ? 6 : prev - 1));
  };
  
  const goToNextDay = () => {
    setCurrentDay((prev) => (prev === 6 ? 0 : prev + 1));
  };
  
  // Handle appointment slot click
  const handleTimeSlotClick = (time: string) => {
    setSelectedTimeSlot(time);
    setShowAppointmentDialog(true);
  };
  
  // Handle appointment click
  const handleAppointmentClick = (appointment: Appointment) => {
    setSelectedAppointment(appointment);
    setShowAppointmentDetails(true);
  };
  
  // Book appointment
  const handleBookAppointment = () => {
    toast.success('Appointment booked successfully');
    setShowAppointmentDialog(false);
  };
  
  // Cancel appointment
  const handleCancelAppointment = () => {
    toast.success('Appointment cancelled');
    setShowAppointmentDetails(false);
  };
  
  // Check if a time slot has an appointment
  const getAppointmentForTimeSlot = (time: string) => {
    return currentDayAppointments.find(appointment => appointment.time === time);
  };
  
  return (
    <DashboardLayout userRole="receptionist">
      <div className="flex justify-between items-center mb-6">
        <div>
          <h2 className="text-2xl font-bold">Appointments</h2>
          <p className="text-muted-foreground">Schedule and manage patient appointments</p>
        </div>
        
        <div className="flex items-center gap-4">
          <div className="relative">
            <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-muted-foreground" />
            <Input 
              placeholder="Search patients..." 
              className="pl-10 w-[240px]" 
            />
          </div>
          
          <Button
            onClick={() => toast.info('Register patient form would open here')}
            variant="outline"
            className="gap-2"
          >
            <UserPlus size={16} /> Register Patient
          </Button>
        </div>
      </div>
      
      <Tabs defaultValue="calendar">
        <TabsList>
          <TabsTrigger value="calendar">Calendar</TabsTrigger>
          <TabsTrigger value="list">List View</TabsTrigger>
        </TabsList>
        
        <TabsContent value="calendar" className="mt-6">
          <Card>
            <CardContent className="p-6">
              {/* Calendar Header */}
              <div className="flex items-center justify-between mb-6">
                <div className="flex items-center gap-2">
                  <CalendarIcon className="h-5 w-5 text-muted-foreground" />
                  <h3 className="text-lg font-medium">July 2025</h3>
                </div>
                
                <div className="flex items-center gap-4">
                  <Button
                    variant="outline"
                    size="icon"
                    onClick={goToPreviousDay}
                  >
                    <ChevronLeft className="h-4 w-4" />
                  </Button>
                  
                  <h3 className="text-lg font-medium w-32 text-center">
                    {daysOfWeek[currentDay]}
                  </h3>
                  
                  <Button
                    variant="outline"
                    size="icon"
                    onClick={goToNextDay}
                  >
                    <ChevronRight className="h-4 w-4" />
                  </Button>
                </div>
                
                <Button
                  variant="outline"
                  onClick={() => setCurrentDay(new Date().getDay() === 0 ? 6 : new Date().getDay() - 1)}
                >
                  Today
                </Button>
              </div>
              
              {/* Calendar Grid */}
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                {/* Morning Slots */}
                <div>
                  <h4 className="text-sm font-medium mb-4">Morning</h4>
                  <div className="space-y-2">
                    {timeSlots.slice(0, 8).map((time) => {
                      const appointment = getAppointmentForTimeSlot(time);
                      return (
                        <div
                          key={time}
                          className={`p-3 rounded-lg border ${
                            appointment 
                              ? 'border-primary/20 bg-primary/5' 
                              : 'border-border hover:border-primary/20 hover:bg-muted/20 cursor-pointer'
                          }`}
                          onClick={() => appointment ? handleAppointmentClick(appointment) : handleTimeSlotClick(time)}
                        >
                          <div className="flex justify-between items-center">
                            <div className="flex items-center gap-2">
                              <Clock className="h-4 w-4 text-muted-foreground" />
                              <span>{time}</span>
                            </div>
                            
                            {!appointment && (
                              <Button
                                variant="ghost"
                                size="icon"
                                className="h-6 w-6"
                                onClick={(e) => {
                                  e.stopPropagation();
                                  handleTimeSlotClick(time);
                                }}
                              >
                                <Plus className="h-4 w-4" />
                              </Button>
                            )}
                          </div>
                          
                          {appointment && (
                            <motion.div
                              initial={{ opacity: 0, y: 5 }}
                              animate={{ opacity: 1, y: 0 }}
                              className="mt-2 flex items-center gap-3"
                            >
                              <Avatar className="h-6 w-6">
                                <AvatarImage src={`https://api.dicebear.com/6.x/initials/svg?seed=${appointment.patientName}`} />
                                <AvatarFallback>{appointment.patientName.charAt(0)}</AvatarFallback>
                              </Avatar>
                              <div>
                                <p className="text-sm font-medium">{appointment.patientName}</p>
                                <p className="text-xs text-muted-foreground">{appointment.type} • {appointment.doctor}</p>
                              </div>
                            </motion.div>
                          )}
                        </div>
                      );
                    })}
                  </div>
                </div>
                
                {/* Afternoon Slots */}
                <div>
                  <h4 className="text-sm font-medium mb-4">Afternoon</h4>
                  <div className="space-y-2">
                    {timeSlots.slice(8).map((time) => {
                      const appointment = getAppointmentForTimeSlot(time);
                      return (
                        <div
                          key={time}
                          className={`p-3 rounded-lg border ${
                            appointment 
                              ? 'border-primary/20 bg-primary/5' 
                              : 'border-border hover:border-primary/20 hover:bg-muted/20 cursor-pointer'
                          }`}
                          onClick={() => appointment ? handleAppointmentClick(appointment) : handleTimeSlotClick(time)}
                        >
                          <div className="flex justify-between items-center">
                            <div className="flex items-center gap-2">
                              <Clock className="h-4 w-4 text-muted-foreground" />
                              <span>{time}</span>
                            </div>
                            
                            {!appointment && (
                              <Button
                                variant="ghost"
                                size="icon"
                                className="h-6 w-6"
                                onClick={(e) => {
                                  e.stopPropagation();
                                  handleTimeSlotClick(time);
                                }}
                              >
                                <Plus className="h-4 w-4" />
                              </Button>
                            )}
                          </div>
                          
                          {appointment && (
                            <motion.div
                              initial={{ opacity: 0, y: 5 }}
                              animate={{ opacity: 1, y: 0 }}
                              className="mt-2 flex items-center gap-3"
                            >
                              <Avatar className="h-6 w-6">
                                <AvatarImage src={`https://api.dicebear.com/6.x/initials/svg?seed=${appointment.patientName}`} />
                                <AvatarFallback>{appointment.patientName.charAt(0)}</AvatarFallback>
                              </Avatar>
                              <div>
                                <p className="text-sm font-medium">{appointment.patientName}</p>
                                <p className="text-xs text-muted-foreground">{appointment.type} • {appointment.doctor}</p>
                              </div>
                            </motion.div>
                          )}
                        </div>
                      );
                    })}
                  </div>
                </div>
              </div>
            </CardContent>
          </Card>
        </TabsContent>
        
        <TabsContent value="list" className="mt-6">
          <Card>
            <CardContent className="p-6">
              <div className="flex justify-between items-center mb-6">
                <h3 className="text-lg font-medium">Upcoming Appointments</h3>
                
                <div className="flex items-center gap-2">
                  <Select defaultValue="all">
                    <SelectTrigger className="w-[160px]">
                      <SelectValue placeholder="Filter by status" />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="all">All Appointments</SelectItem>
                      <SelectItem value="scheduled">Scheduled</SelectItem>
                      <SelectItem value="in-progress">In Progress</SelectItem>
                      <SelectItem value="completed">Completed</SelectItem>
                      <SelectItem value="cancelled">Cancelled</SelectItem>
                    </SelectContent>
                  </Select>
                </div>
              </div>
              
              <div className="space-y-4">
                {/* Flatten all appointments into a single array */}
                {Object.values(mockAppointments)
                  .flat()
                  .sort((a, b) => {
                    const dayA = Object.keys(mockAppointments).findIndex(day => 
                      mockAppointments[day].some(appt => appt.id === a.id)
                    );
                    const dayB = Object.keys(mockAppointments).findIndex(day => 
                      mockAppointments[day].some(appt => appt.id === b.id)
                    );
                    return dayA - dayB;
                  })
                  .map((appointment) => {
                    const day = Object.keys(mockAppointments).find(day => 
                      mockAppointments[day].some(appt => appt.id === appointment.id)
                    );
                    
                    return (
                      <div 
                        key={appointment.id}
                        className="p-4 border border-border rounded-lg hover:border-primary/20 hover:bg-muted/20 cursor-pointer"
                        onClick={() => handleAppointmentClick(appointment)}
                      >
                        <div className="flex items-center justify-between">
                          <div className="flex items-center gap-3">
                            <Avatar className="h-10 w-10">
                              <AvatarImage src={`https://api.dicebear.com/6.x/initials/svg?seed=${appointment.patientName}`} />
                              <AvatarFallback>{appointment.patientName.charAt(0)}</AvatarFallback>
                            </Avatar>
                            <div>
                              <p className="font-medium">{appointment.patientName}</p>
                              <p className="text-xs text-muted-foreground">ID: {appointment.patientId}</p>
                            </div>
                          </div>
                          
                          <div className="text-right">
                            <p className="text-sm">{day} • {appointment.time}</p>
                            <p className="text-xs text-muted-foreground">{appointment.duration} • {appointment.doctor}</p>
                          </div>
                        </div>
                        
                        <div className="mt-2 flex items-center justify-between">
                          <span className={`text-xs px-2 py-1 rounded-full ${
                            appointment.type === 'consultation' ? 'bg-primary/10 text-primary' :
                            appointment.type === 'follow-up' ? 'bg-secondary/10 text-secondary' :
                            'bg-accent/10 text-accent'
                          }`}>
                            {appointment.type.charAt(0).toUpperCase() + appointment.type.slice(1)}
                          </span>
                          
                          <span className={`text-xs px-2 py-1 rounded-full ${
                            appointment.status === 'scheduled' ? 'bg-muted text-muted-foreground' :
                            appointment.status === 'in-progress' ? 'bg-primary/10 text-primary' :
                            appointment.status === 'completed' ? 'bg-secondary/10 text-secondary' :
                            'bg-destructive/10 text-destructive'
                          }`}>
                            {appointment.status.charAt(0).toUpperCase() + appointment.status.slice(1)}
                          </span>
                        </div>
                      </div>
                    );
                  })}
                  
                {Object.values(mockAppointments).flat().length === 0 && (
                  <div className="flex flex-col items-center justify-center py-12">
                    <CalendarIcon className="h-12 w-12 text-muted-foreground mb-4" />
                    <h4 className="text-lg font-medium">No Appointments</h4>
                    <p className="text-muted-foreground">There are no appointments scheduled</p>
                  </div>
                )}
              </div>
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>
      
      {/* New Appointment Dialog */}
      <Dialog open={showAppointmentDialog} onOpenChange={setShowAppointmentDialog}>
        <DialogContent className="sm:max-w-[500px]">
          <DialogHeader>
            <DialogTitle>Book New Appointment</DialogTitle>
            <DialogDescription>
              {selectedTimeSlot ? `Schedule an appointment for ${selectedTimeSlot} on ${daysOfWeek[currentDay]}` : 'Schedule a new appointment'}
            </DialogDescription>
          </DialogHeader>
          
          <div className="grid gap-4 py-4">
            <div className="space-y-2">
              <Label htmlFor="patient">Patient</Label>
              <div className="relative">
                <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-muted-foreground" />
                <Input id="patient" placeholder="Search patient by name or ID" className="pl-10" />
              </div>
              <p className="text-xs text-muted-foreground">
                Patient not found? <a className="text-primary hover:underline cursor-pointer">Register new patient</a>
              </p>
            </div>
            
            <div className="grid grid-cols-2 gap-4">
              <div className="space-y-2">
                <Label htmlFor="date">Date</Label>
                <Input id="date" type="date" />
              </div>
              <div className="space-y-2">
                <Label htmlFor="time">Time</Label>
                <Select defaultValue={selectedTimeSlot || undefined}>
                  <SelectTrigger>
                    <SelectValue placeholder="Select time" />
                  </SelectTrigger>
                  <SelectContent>
                    {timeSlots.map((time) => (
                      <SelectItem key={time} value={time}>{time}</SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>
            </div>
            
            <div className="space-y-2">
              <Label htmlFor="doctor">Doctor</Label>
              <Select defaultValue="dr-mehta">
                <SelectTrigger>
                  <SelectValue placeholder="Select doctor" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="dr-mehta">Dr. Mehta</SelectItem>
                  <SelectItem value="dr-shah">Dr. Shah</SelectItem>
                </SelectContent>
              </Select>
            </div>
            
            <div className="space-y-2">
              <Label htmlFor="type">Appointment Type</Label>
              <Select defaultValue="consultation">
                <SelectTrigger>
                  <SelectValue placeholder="Select type" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="consultation">Consultation</SelectItem>
                  <SelectItem value="follow-up">Follow-up</SelectItem>
                  <SelectItem value="procedure">Procedure</SelectItem>
                </SelectContent>
              </Select>
            </div>
            
            <div className="space-y-2">
              <Label htmlFor="notes">Notes (Optional)</Label>
              <Input id="notes" placeholder="Any additional notes" />
            </div>
          </div>
          
          <DialogFooter>
            <Button variant="outline" onClick={() => setShowAppointmentDialog(false)}>
              Cancel
            </Button>
            <Button onClick={handleBookAppointment}>
              Book Appointment
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
      
      {/* Appointment Details Dialog */}
      <Dialog open={showAppointmentDetails} onOpenChange={setShowAppointmentDetails}>
        {selectedAppointment && (
          <DialogContent className="sm:max-w-[500px]">
            <DialogHeader>
              <DialogTitle>Appointment Details</DialogTitle>
              <DialogDescription>
                Appointment for {selectedAppointment.patientName}
              </DialogDescription>
            </DialogHeader>
            
            <div className="py-4">
              <div className="flex items-center gap-4 mb-6">
                <Avatar className="h-16 w-16">
                  <AvatarImage src={`https://api.dicebear.com/6.x/initials/svg?seed=${selectedAppointment.patientName}`} />
                  <AvatarFallback>{selectedAppointment.patientName.charAt(0)}</AvatarFallback>
                </Avatar>
                <div>
                  <h3 className="text-lg font-medium">{selectedAppointment.patientName}</h3>
                  <p className="text-sm text-muted-foreground">ID: {selectedAppointment.patientId}</p>
                </div>
              </div>
              
              <div className="space-y-4">
                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <p className="text-sm text-muted-foreground">Date</p>
                    <p className="font-medium">{daysOfWeek[currentDay]}, July 21, 2025</p>
                  </div>
                  <div>
                    <p className="text-sm text-muted-foreground">Time</p>
                    <p className="font-medium">{selectedAppointment.time} ({selectedAppointment.duration})</p>
                  </div>
                </div>
                
                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <p className="text-sm text-muted-foreground">Doctor</p>
                    <p className="font-medium">{selectedAppointment.doctor}</p>
                  </div>
                  <div>
                    <p className="text-sm text-muted-foreground">Type</p>
                    <p className="font-medium">{selectedAppointment.type.charAt(0).toUpperCase() + selectedAppointment.type.slice(1)}</p>
                  </div>
                </div>
                
                <div>
                  <p className="text-sm text-muted-foreground">Status</p>
                  <p className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${
                    selectedAppointment.status === 'scheduled' ? 'bg-muted text-muted-foreground' :
                    selectedAppointment.status === 'in-progress' ? 'bg-primary/10 text-primary' :
                    selectedAppointment.status === 'completed' ? 'bg-secondary/10 text-secondary' :
                    'bg-destructive/10 text-destructive'
                  }`}>
                    {selectedAppointment.status.charAt(0).toUpperCase() + selectedAppointment.status.slice(1)}
                  </p>
                </div>
                
                <div>
                  <p className="text-sm text-muted-foreground">Notes</p>
                  <p className="font-medium">No notes added</p>
                </div>
              </div>
            </div>
            
            <DialogFooter className="flex flex-col-reverse sm:flex-row sm:justify-between sm:space-x-2">
              <Button variant="outline" className="mt-3 sm:mt-0" onClick={handleCancelAppointment}>
                Cancel Appointment
              </Button>
              <div className="space-x-2">
                <Button variant="outline" onClick={() => setShowAppointmentDetails(false)}>
                  Close
                </Button>
                <Button onClick={() => {
                  setShowAppointmentDetails(false);
                  toast.success('Appointment details updated');
                }}>
                  Edit
                </Button>
              </div>
            </DialogFooter>
          </DialogContent>
        )}
      </Dialog>
    </DashboardLayout>
  );
}