import { useState } from 'react';
import { motion } from 'framer-motion';
import { Link, useNavigate } from 'react-router-dom';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { toast } from 'sonner';

export default function Login() {
  const navigate = useNavigate();
  const [isLoading, setIsLoading] = useState(false);

  const handleLogin = (e: React.FormEvent) => {
    e.preventDefault();
    setIsLoading(true);
    
    // Simulate API call
    setTimeout(() => {
      setIsLoading(false);
      toast.error('Login is disabled in the demo version');
    }, 1500);
  };

  const handleRoleLogin = (role: string) => {
    toast.success(`Entering as ${role}`);
    navigate('/dashboard/queue');
  };

  return (
    <div className="min-h-screen flex items-center justify-center bg-background p-4">
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5 }}
        className="w-full max-w-md"
      >
        <Card className="border-border bg-card">
          <CardHeader className="space-y-1 text-center">
            <div className="flex justify-center mb-4">
              <Link to="/">
                <img 
                  src="https://storage.googleapis.com/fenado-ai-farm-public/generated/bae86827-3b88-4061-92bb-2de0b1eec00d.webp" 
                  alt="MedMitra Logo" 
                  className="h-12 w-auto"
                />
              </Link>
            </div>
            <CardTitle className="text-2xl font-bold">Clinic Login</CardTitle>
            <CardDescription className="text-muted-foreground">
              Enter your credentials to access your clinic dashboard
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            <form onSubmit={handleLogin} className="space-y-4">
              <div className="space-y-2">
                <Label htmlFor="email">Email</Label>
                <Input 
                  id="email" 
                  type="email" 
                  placeholder="name@clinic.com" 
                  required 
                  disabled={isLoading}
                />
              </div>
              <div className="space-y-2">
                <div className="flex items-center justify-between">
                  <Label htmlFor="password">Password</Label>
                  <a 
                    href="#" 
                    className="text-sm text-primary hover:underline"
                    onClick={(e) => {
                      e.preventDefault();
                      toast.info('Password reset is disabled in the demo');
                    }}
                  >
                    Forgot password?
                  </a>
                </div>
                <Input 
                  id="password" 
                  type="password" 
                  placeholder="••••••••" 
                  required 
                  disabled={isLoading}
                />
              </div>
              <Button 
                type="submit" 
                className="w-full bg-primary text-primary-foreground hover:bg-gradient-to-r hover:from-primary hover:to-secondary"
                disabled={isLoading}
              >
                {isLoading ? 'Logging in...' : 'Login'}
              </Button>
            </form>
            
            <div className="relative">
              <div className="absolute inset-0 flex items-center">
                <span className="w-full border-t border-border" />
              </div>
              <div className="relative flex justify-center text-xs uppercase">
                <span className="bg-card px-2 text-muted-foreground">
                  Skip for Demo
                </span>
              </div>
            </div>
            
            <div className="grid grid-cols-2 gap-4">
              <Button 
                variant="outline" 
                className="border-primary text-primary hover:bg-primary hover:text-primary-foreground"
                onClick={() => handleRoleLogin('Admin')}
              >
                Enter as Admin
              </Button>
              <Button 
                variant="outline" 
                className="border-secondary text-secondary hover:bg-secondary hover:text-secondary-foreground"
                onClick={() => handleRoleLogin('Doctor')}
              >
                Enter as Doctor
              </Button>
              <Button 
                variant="outline" 
                className="border-accent text-accent hover:bg-accent hover:text-accent-foreground"
                onClick={() => handleRoleLogin('Receptionist')}
              >
                Enter as Receptionist
              </Button>
              <Button 
                variant="outline" 
                className="border-muted text-muted-foreground hover:bg-muted hover:text-background"
                onClick={() => handleRoleLogin('Optometrist')}
              >
                Enter as Optometrist
              </Button>
            </div>
          </CardContent>
          <CardFooter className="flex justify-center">
            <Link to="/" className="text-sm text-muted-foreground hover:text-foreground">
              ← Back to Home
            </Link>
          </CardFooter>
        </Card>
      </motion.div>
    </div>
  );
}