import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import { Toaster } from 'sonner';
import { ThemeProvider } from '@/components/theme-provider';
import Home from './pages/Home';
import Login from './pages/Login';
import Contact from './pages/Contact';
import ErrorPage from './pages/ErrorPage';
import Queue from './pages/dashboard/Queue';
import EMR from './pages/dashboard/EMR';
import Reports from './pages/dashboard/Reports';
import Appointments from './pages/dashboard/Appointments';
import Patients from './pages/dashboard/Patients';
import Billing from './pages/dashboard/Billing';

function getRouterBasename() {
  const pathSegments = window.location.pathname.split('/').filter(Boolean);
  if (pathSegments.length === 0) return '/';
  const indexOfSrc = pathSegments.findIndex((seg) => seg === 'src');
  if (indexOfSrc !== -1) {
    return '/' + pathSegments.slice(0, indexOfSrc).join('/');
  }
  return '/' + pathSegments.slice(0, -1).join('/');
}

export default function App() {
  return (
    <ThemeProvider
      attribute="class"
      defaultTheme="system"
      enableSystem
      disableTransitionOnChange
    >
      <Router basename={getRouterBasename()}>
        <Toaster position="top-right" richColors />
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/login" element={<Login />} />
          <Route path="/contact" element={<Contact />} />
          
          {/* Dashboard Routes */}
          <Route path="/dashboard/queue" element={<Queue />} />
          <Route path="/dashboard/emr/:patientId" element={<EMR />} />
          <Route path="/dashboard/reports" element={<Reports />} />
          <Route path="/dashboard/appointments" element={<Appointments />} />
          <Route path="/dashboard/patients" element={<Patients />} />
          <Route path="/dashboard/billing" element={<Billing />} />
          
          {/* Catch all route */}
          <Route path="*" element={<ErrorPage />} />
        </Routes>
      </Router>
    </ThemeProvider>
  );
}