import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { DashboardLayout } from '@/components/layout';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';
import { Dialog, DialogContent, DialogDescription, DialogFooter, DialogHeader, DialogTitle } from '@/components/ui/dialog';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar';
import { Search, Download, Printer, Plus, CreditCard, Trash2, Banknote, CheckCircle, Calendar, ChevronRight } from 'lucide-react';
import { toast } from 'sonner';

// Types
type Patient = {
  id: string;
  name: string;
  patientId: string;
  doctor: string;
  amount: number;
  status: 'pending' | 'paid' | 'partial';
  date: string;
};

// Mock patients data
const pendingPatients: Patient[] = [
  {
    id: 'b1',
    name: 'Rahul Singh',
    patientId: 'DCLNC-2024-101',
    doctor: 'Dr. Mehta',
    amount: 2500,
    status: 'pending',
    date: '2024-07-21',
  },
  {
    id: 'b2',
    name: 'Priya Sharma',
    patientId: 'DCLNC-2024-102',
    doctor: 'Dr. Mehta',
    amount: 3800,
    status: 'pending',
    date: '2024-07-21',
  },
  {
    id: 'b3',
    name: 'Amit Patel',
    patientId: 'DCLNC-2024-103',
    doctor: 'Dr. Shah',
    amount: 1500,
    status: 'pending',
    date: '2024-07-21',
  }
];

// Mock invoice items
const mockInvoiceItems = [
  {
    id: 'i1',
    description: 'Consultation Fee',
    quantity: 1,
    unitPrice: 1500,
    amount: 1500
  },
  {
    id: 'i2',
    description: 'Fundus Examination',
    quantity: 1,
    unitPrice: 800,
    amount: 800
  },
  {
    id: 'i3',
    description: 'Eye Drops - Refresh Tears',
    quantity: 2,
    unitPrice: 350,
    amount: 700
  }
];

export default function BillingPage() {
  const navigate = useNavigate();
  const [searchQuery, setSearchQuery] = useState('');
  const [invoiceDialogOpen, setInvoiceDialogOpen] = useState(false);
  const [paymentDialogOpen, setPaymentDialogOpen] = useState(false);
  const [addItemDialogOpen, setAddItemDialogOpen] = useState(false);
  const [selectedPatient, setSelectedPatient] = useState<Patient | null>(null);
  const [invoiceItems, setInvoiceItems] = useState(mockInvoiceItems);
  const [discount, setDiscount] = useState(0);
  
  // Filter patients based on search query
  const filteredPatients = pendingPatients.filter(patient => 
    patient.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
    patient.patientId.toLowerCase().includes(searchQuery.toLowerCase()) ||
    patient.doctor.toLowerCase().includes(searchQuery.toLowerCase())
  );
  
  // Calculate subtotal
  const subtotal = invoiceItems.reduce((total, item) => total + item.amount, 0);
  
  // Calculate total after discount
  const total = subtotal - discount;
  
  // Handle view invoice
  const handleViewInvoice = (patient: Patient) => {
    setSelectedPatient(patient);
    setInvoiceDialogOpen(true);
  };
  
  // Handle add item
  const handleAddItem = () => {
    const newItem = {
      id: `i${Date.now()}`,
      description: 'New Item',
      quantity: 1,
      unitPrice: 500,
      amount: 500
    };
    setInvoiceItems([...invoiceItems, newItem]);
    setAddItemDialogOpen(false);
    toast.success('Item added to invoice');
  };
  
  // Handle remove item
  const handleRemoveItem = (id: string) => {
    setInvoiceItems(invoiceItems.filter(item => item.id !== id));
    toast.success('Item removed from invoice');
  };
  
  // Handle payment
  const handlePayment = () => {
    setPaymentDialogOpen(false);
    setInvoiceDialogOpen(false);
    toast.success('Payment received successfully');
  };
  
  return (
    <DashboardLayout userRole="receptionist">
      <div className="flex justify-between items-center mb-6">
        <div>
          <h2 className="text-2xl font-bold">Billing</h2>
          <p className="text-muted-foreground">Manage patient invoices and payments</p>
        </div>
        
        <div className="flex items-center gap-4">
          <Button
            variant="outline"
            className="gap-2"
            onClick={() => toast.success('Daily report downloaded')}
          >
            <Download size={16} /> Daily Report
          </Button>
        </div>
      </div>
      
      <Tabs defaultValue="pending">
        <TabsList className="mb-6">
          <TabsTrigger value="pending">Pending Payments</TabsTrigger>
          <TabsTrigger value="paid">Paid Invoices</TabsTrigger>
          <TabsTrigger value="all">All Transactions</TabsTrigger>
        </TabsList>
        
        <TabsContent value="pending">
          <Card className="mb-6">
            <CardContent className="p-4">
              <div className="flex flex-col md:flex-row md:items-center gap-4">
                <div className="relative flex-1">
                  <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-muted-foreground" />
                  <Input 
                    placeholder="Search by name, ID, or doctor..." 
                    className="pl-10" 
                    value={searchQuery}
                    onChange={(e) => setSearchQuery(e.target.value)}
                  />
                </div>
                
                <div className="flex items-center gap-4">
                  <Select defaultValue="today">
                    <SelectTrigger className="w-[160px]">
                      <SelectValue placeholder="Filter by date" />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="today">Today</SelectItem>
                      <SelectItem value="yesterday">Yesterday</SelectItem>
                      <SelectItem value="thisWeek">This Week</SelectItem>
                      <SelectItem value="thisMonth">This Month</SelectItem>
                      <SelectItem value="custom">Custom Range</SelectItem>
                    </SelectContent>
                  </Select>
                </div>
              </div>
            </CardContent>
          </Card>
          
          <Card>
            <CardContent className="p-0">
              <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead>Patient ID</TableHead>
                    <TableHead>Name</TableHead>
                    <TableHead>Doctor</TableHead>
                    <TableHead>Amount</TableHead>
                    <TableHead>Status</TableHead>
                    <TableHead className="text-right">Actions</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {filteredPatients.map((patient) => (
                    <TableRow key={patient.id}>
                      <TableCell className="font-medium">{patient.patientId}</TableCell>
                      <TableCell>
                        <div className="flex items-center gap-3">
                          <Avatar className="h-8 w-8">
                            <AvatarImage src={`https://api.dicebear.com/6.x/initials/svg?seed=${patient.name}`} />
                            <AvatarFallback>{patient.name.charAt(0)}</AvatarFallback>
                          </Avatar>
                          <span>{patient.name}</span>
                        </div>
                      </TableCell>
                      <TableCell>{patient.doctor}</TableCell>
                      <TableCell className="font-medium">₹{patient.amount}</TableCell>
                      <TableCell>
                        <span className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${
                          patient.status === 'pending' ? 'bg-muted text-muted-foreground' :
                          patient.status === 'paid' ? 'bg-secondary/10 text-secondary' :
                          'bg-primary/10 text-primary'
                        }`}>
                          {patient.status.charAt(0).toUpperCase() + patient.status.slice(1)}
                        </span>
                      </TableCell>
                      <TableCell className="text-right">
                        <Button
                          variant="outline"
                          size="sm"
                          className="gap-1"
                          onClick={() => handleViewInvoice(patient)}
                        >
                          <ChevronRight size={16} /> View Invoice
                        </Button>
                      </TableCell>
                    </TableRow>
                  ))}
                  
                  {filteredPatients.length === 0 && (
                    <TableRow>
                      <TableCell colSpan={6} className="text-center py-8">
                        <div className="flex flex-col items-center justify-center">
                          <CreditCard className="h-12 w-12 text-muted-foreground mb-4" />
                          <h4 className="text-lg font-medium">No pending payments</h4>
                          <p className="text-muted-foreground">All invoices have been paid</p>
                        </div>
                      </TableCell>
                    </TableRow>
                  )}
                </TableBody>
              </Table>
            </CardContent>
          </Card>
        </TabsContent>
        
        <TabsContent value="paid">
          <Card>
            <CardContent className="p-6">
              <div className="flex flex-col items-center justify-center py-12 text-center">
                <Calendar className="h-12 w-12 text-muted-foreground mb-4" />
                <h4 className="text-lg font-medium">No transactions today</h4>
                <p className="text-muted-foreground mb-6">Payments made today will appear here</p>
                <Button
                  variant="outline"
                  onClick={() => toast.info('View history dialog would open here')}
                >
                  View Payment History
                </Button>
              </div>
            </CardContent>
          </Card>
        </TabsContent>
        
        <TabsContent value="all">
          <Card>
            <CardContent className="p-6">
              <div className="flex flex-col items-center justify-center py-12 text-center">
                <CreditCard className="h-12 w-12 text-muted-foreground mb-4" />
                <h4 className="text-lg font-medium">Transaction History</h4>
                <p className="text-muted-foreground mb-6">Select a date range to view transaction history</p>
                <div className="flex items-center gap-4">
                  <Input type="date" className="w-40" />
                  <span>to</span>
                  <Input type="date" className="w-40" />
                  <Button>Search</Button>
                </div>
              </div>
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>
      
      {/* Invoice Dialog */}
      <Dialog open={invoiceDialogOpen} onOpenChange={setInvoiceDialogOpen}>
        <DialogContent className="sm:max-w-[700px]">
          <DialogHeader>
            <DialogTitle>Invoice</DialogTitle>
            <DialogDescription>
              {selectedPatient ? `Invoice for ${selectedPatient.name} (${selectedPatient.patientId})` : ''}
            </DialogDescription>
          </DialogHeader>
          
          <div className="py-4">
            {/* Invoice Header */}
            <div className="flex flex-col md:flex-row justify-between mb-6">
              <div>
                <h3 className="font-bold">Dholakia Eye Clinic</h3>
                <p className="text-sm text-muted-foreground">123 Health-Tech Avenue</p>
                <p className="text-sm text-muted-foreground">MedCity, MC 12345</p>
              </div>
              
              <div className="text-right">
                <p className="font-medium">Invoice #INV-{Date.now().toString().slice(-6)}</p>
                <p className="text-sm text-muted-foreground">Date: {new Date().toLocaleDateString()}</p>
              </div>
            </div>
            
            {/* Patient Info */}
            {selectedPatient && (
              <div className="flex items-center gap-4 mb-6 p-4 border border-border rounded-lg bg-muted/20">
                <Avatar className="h-12 w-12">
                  <AvatarImage src={`https://api.dicebear.com/6.x/initials/svg?seed=${selectedPatient.name}`} />
                  <AvatarFallback>{selectedPatient.name.charAt(0)}</AvatarFallback>
                </Avatar>
                <div>
                  <h3 className="font-medium">{selectedPatient.name}</h3>
                  <p className="text-sm text-muted-foreground">ID: {selectedPatient.patientId}</p>
                </div>
                <div className="ml-auto text-right">
                  <p className="text-sm">Doctor</p>
                  <p className="font-medium">{selectedPatient.doctor}</p>
                </div>
              </div>
            )}
            
            {/* Invoice Items */}
            <div className="mb-6">
              <div className="flex justify-between items-center mb-2">
                <h4 className="font-medium">Invoice Items</h4>
                <Button
                  variant="outline"
                  size="sm"
                  className="gap-1"
                  onClick={() => setAddItemDialogOpen(true)}
                >
                  <Plus size={14} /> Add Item
                </Button>
              </div>
              
              <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead>Description</TableHead>
                    <TableHead className="text-right">Qty</TableHead>
                    <TableHead className="text-right">Unit Price</TableHead>
                    <TableHead className="text-right">Amount</TableHead>
                    <TableHead className="text-right w-[50px]"></TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {invoiceItems.map((item) => (
                    <TableRow key={item.id}>
                      <TableCell>{item.description}</TableCell>
                      <TableCell className="text-right">{item.quantity}</TableCell>
                      <TableCell className="text-right">₹{item.unitPrice}</TableCell>
                      <TableCell className="text-right font-medium">₹{item.amount}</TableCell>
                      <TableCell className="text-right">
                        <Button
                          variant="ghost"
                          size="icon"
                          onClick={() => handleRemoveItem(item.id)}
                        >
                          <Trash2 size={14} className="text-destructive" />
                        </Button>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </div>
            
            {/* Invoice Summary */}
            <div className="space-y-2 ml-auto w-full max-w-xs">
              <div className="flex justify-between text-sm">
                <span className="text-muted-foreground">Subtotal:</span>
                <span>₹{subtotal}</span>
              </div>
              <div className="flex justify-between text-sm">
                <span className="text-muted-foreground">Discount:</span>
                <div className="flex items-center gap-2">
                  <Input 
                    type="number" 
                    className="w-16 h-6 text-xs" 
                    value={discount}
                    onChange={(e) => setDiscount(Number(e.target.value))}
                  />
                  <span>₹</span>
                </div>
              </div>
              <div className="flex justify-between font-bold pt-2 border-t border-border">
                <span>Total:</span>
                <span>₹{total}</span>
              </div>
            </div>
          </div>
          
          <DialogFooter className="flex flex-col-reverse sm:flex-row sm:justify-between sm:space-x-2">
            <div className="flex gap-2 mt-3 sm:mt-0">
              <Button
                variant="outline"
                className="gap-1"
                onClick={() => toast.success('Invoice printed')}
              >
                <Printer size={16} /> Print
              </Button>
              <Button
                variant="outline"
                className="gap-1"
                onClick={() => toast.success('Invoice downloaded')}
              >
                <Download size={16} /> Download
              </Button>
            </div>
            <div className="space-x-2">
              <Button variant="outline" onClick={() => setInvoiceDialogOpen(false)}>
                Close
              </Button>
              <Button
                className="gap-1 bg-primary text-primary-foreground hover:bg-gradient-to-r hover:from-primary hover:to-secondary glow-effect"
                onClick={() => setPaymentDialogOpen(true)}
              >
                <CreditCard size={16} /> Receive Payment
              </Button>
            </div>
          </DialogFooter>
        </DialogContent>
      </Dialog>
      
      {/* Payment Dialog */}
      <Dialog open={paymentDialogOpen} onOpenChange={setPaymentDialogOpen}>
        <DialogContent className="sm:max-w-[500px]">
          <DialogHeader>
            <DialogTitle>Receive Payment</DialogTitle>
            <DialogDescription>
              Process payment for the invoice
            </DialogDescription>
          </DialogHeader>
          
          <div className="py-4">
            <div className="bg-muted/20 p-4 rounded-lg mb-6 text-center">
              <p className="text-sm text-muted-foreground">Invoice Total</p>
              <p className="text-3xl font-bold">₹{total}</p>
            </div>
            
            <div className="space-y-4">
              <div className="space-y-2">
                <Label htmlFor="paymentMethod">Payment Method</Label>
                <Select defaultValue="cash">
                  <SelectTrigger>
                    <SelectValue placeholder="Select payment method" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="cash">Cash</SelectItem>
                    <SelectItem value="card">Credit/Debit Card</SelectItem>
                    <SelectItem value="upi">UPI</SelectItem>
                    <SelectItem value="netbanking">Net Banking</SelectItem>
                  </SelectContent>
                </Select>
              </div>
              
              <div className="space-y-2">
                <Label htmlFor="amountReceived">Amount Received</Label>
                <Input id="amountReceived" type="number" defaultValue={total} />
              </div>
              
              <div className="space-y-2">
                <Label htmlFor="notes">Notes (Optional)</Label>
                <Input id="notes" placeholder="Any additional notes about the payment" />
              </div>
              
              <div className="space-y-2">
                <Label>Send Receipt</Label>
                <div className="flex items-center gap-2">
                  <Input placeholder="Email or phone number" />
                  <Button variant="outline" size="icon">
                    <CheckCircle size={16} />
                  </Button>
                </div>
              </div>
            </div>
          </div>
          
          <DialogFooter>
            <Button variant="outline" onClick={() => setPaymentDialogOpen(false)}>
              Cancel
            </Button>
            <Button
              className="gap-1 bg-primary text-primary-foreground hover:bg-gradient-to-r hover:from-primary hover:to-secondary glow-effect"
              onClick={handlePayment}
            >
              <Banknote size={16} /> Complete Payment
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
      
      {/* Add Item Dialog */}
      <Dialog open={addItemDialogOpen} onOpenChange={setAddItemDialogOpen}>
        <DialogContent className="sm:max-w-[500px]">
          <DialogHeader>
            <DialogTitle>Add Item</DialogTitle>
            <DialogDescription>
              Add a new item to the invoice
            </DialogDescription>
          </DialogHeader>
          
          <div className="py-4 space-y-4">
            <div className="space-y-2">
              <Label htmlFor="itemDescription">Description</Label>
              <Input id="itemDescription" placeholder="Enter item description" />
            </div>
            
            <div className="grid grid-cols-2 gap-4">
              <div className="space-y-2">
                <Label htmlFor="itemQuantity">Quantity</Label>
                <Input id="itemQuantity" type="number" defaultValue={1} min={1} />
              </div>
              <div className="space-y-2">
                <Label htmlFor="itemPrice">Unit Price (₹)</Label>
                <Input id="itemPrice" type="number" defaultValue={500} min={0} />
              </div>
            </div>
            
            <div className="space-y-2">
              <Label>Category</Label>
              <Select defaultValue="consultation">
                <SelectTrigger>
                  <SelectValue placeholder="Select category" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="consultation">Consultation</SelectItem>
                  <SelectItem value="procedure">Procedure</SelectItem>
                  <SelectItem value="medicine">Medicine</SelectItem>
                  <SelectItem value="glasses">Glasses/Lenses</SelectItem>
                  <SelectItem value="other">Other</SelectItem>
                </SelectContent>
              </Select>
            </div>
          </div>
          
          <DialogFooter>
            <Button variant="outline" onClick={() => setAddItemDialogOpen(false)}>
              Cancel
            </Button>
            <Button onClick={handleAddItem}>
              Add to Invoice
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </DashboardLayout>
  );
}